/* Footer */
.footer {
  position: relative;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
}

.footer-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.footer-glow-1 {
  position: absolute;
  top: -100px;
  left: -100px;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.1) 0%, transparent 70%);
  filter: blur(40px);
}

.footer-glow-2 {
  position: absolute;
  bottom: -100px;
  right: -100px;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.08) 0%, transparent 70%);
  filter: blur(30px);
}

.footer-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 20% 30%, rgba(255, 255, 255, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 255, 255, 0.02) 0%, transparent 50%);
  opacity: 0.5;
}

.container {
  position: relative;
  z-index: 2;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Footer Main */
.footer-main {
  display: grid;
  grid-template-columns: 2fr 1fr 1.5fr;
  gap: 60px;
  padding: 80px 0 60px;
}

/* Brand Section */
.footer-brand {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 15px;
  cursor: pointer;
}

.logo-image {
  height: 50px;
  width: auto;
  filter: 
    brightness(0) invert(1)
    drop-shadow(0 0 10px rgba(255, 7, 58, 0.6));
}

.logo-text {
  display: flex;
  flex-direction: column;
  line-height: 1;
}

.logo-main {
  font-family: 'Exo 2', sans-serif;
  font-size: 2rem;
  font-weight: 800;
  color: #ffffff;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.logo-subtitle {
  font-size: 0.8rem;
  color: #ff073a;
  font-weight: 600;
  letter-spacing: 3px;
  text-transform: uppercase;
}

.logo-fallback {
  font-family: 'Exo 2', sans-serif;
  font-size: 2rem;
  font-weight: 800;
  color: #ffffff;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.footer-tagline {
  color: #b3b3b3;
  line-height: 1.6;
  font-size: 1rem;
  max-width: 400px;
}

/* Social Section */
.social-section {
  margin-top: 20px;
}

.social-title {
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 20px;
}

.social-links {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.social-link {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: #b3b3b3;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.social-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--social-color);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 12px;
}

.social-link:hover::before {
  opacity: 1;
}

.social-link i {
  position: relative;
  z-index: 2;
  transition: color 0.3s ease;
}

.social-link:hover i {
  color: white;
}

/* Links Section */
.footer-links {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.links-title {
  color: #ffffff;
  font-size: 1.1rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 10px;
}

.links-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.footer-link {
  background: none;
  border: none;
  color: #b3b3b3;
  font-size: 0.95rem;
  text-align: left;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 8px 0;
  position: relative;
  font-family: inherit;
}

.footer-link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background: #ff073a;
  transition: width 0.3s ease;
}

.footer-link:hover::before {
  width: 30px;
}

/* Contact Section */
.footer-contact {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.contact-title {
  color: #ffffff;
  font-size: 1.1rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 10px;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #b3b3b3;
  font-size: 0.95rem;
}

.contact-item i {
  color: #ff073a;
  width: 16px;
  font-size: 0.9rem;
}

/* Newsletter */
.newsletter {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  padding: 25px;
  margin-top: 10px;
}

.newsletter-title {
  color: #ffffff;
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 8px;
}

.newsletter-text {
  color: #b3b3b3;
  font-size: 0.85rem;
  margin-bottom: 20px;
}

.newsletter-form {
  display: flex;
  gap: 10px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 5px;
}

.newsletter-input {
  flex: 1;
  background: transparent;
  border: none;
  color: #ffffff;
  padding: 12px 15px;
  font-size: 0.9rem;
  outline: none;
}

.newsletter-input::placeholder {
  color: #666;
}

.newsletter-btn {
  width: 45px;
  height: 45px;
  background: linear-gradient(135deg, #ff073a, #ff073a);
  border: none;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Footer Bottom */
.footer-bottom {
  position: relative;
  padding: 40px 0 30px;
}

.footer-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  margin-bottom: 30px;
}

.bottom-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.copyright {
  color: #666;
  font-size: 0.9rem;
}

.footer-credits {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #666;
  font-size: 0.9rem;
}

.footer-credits i {
  color: #ff073a;
}

/* Back to Top */
.back-to-top {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #ff073a, #ff073a);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 5px 15px rgba(255, 7, 58, 0.3);
}

/* Responsive */
@media (max-width: 1024px) {
  .footer-main {
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
  
  .footer-brand {
    grid-column: 1 / -1;
  }
}

@media (max-width: 768px) {
  .footer-main {
    grid-template-columns: 1fr;
    gap: 40px;
    padding: 60px 0 40px;
  }
  
  .footer-logo {
    justify-content: center;
    text-align: center;
  }
  
  .footer-tagline {
    text-align: center;
    margin: 0 auto;
  }
  
  .social-links {
    justify-content: center;
  }
  
  .footer-links,
  .footer-contact {
    text-align: center;
  }
  
  .links-list {
    align-items: center;
  }
  
  .contact-info {
    align-items: center;
  }
  
  .bottom-content {
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }
  
  .back-to-top {
    position: relative;
    top: auto;
    right: auto;
    transform: none;
    margin: 20px auto 0;
  }
  
  .newsletter-form {
    max-width: 300px;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
  .footer-main {
    padding: 40px 0 30px;
    gap: 30px;
  }
  
  .footer-logo {
    flex-direction: column;
    gap: 10px;
  }
  
  .logo-main {
    font-size: 1.8rem;
  }
  
  .social-links {
    gap: 10px;
  }
  
  .social-link {
    width: 45px;
    height: 45px;
    font-size: 1.1rem;
  }
  
  .newsletter {
    padding: 20px;
  }
}