.loginContainer {
  min-height: 100vh;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.loginBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.loginGlow1 {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.15) 0%, transparent 70%);
  filter: blur(60px);
}

.loginGlow2 {
  position: absolute;
  bottom: 20%;
  right: 10%;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.1) 0%, transparent 70%);
  filter: blur(50px);
}

.loginCard {
  background: linear-gradient(135deg, rgba(26, 26, 36, 0.95), rgba(22, 22, 34, 0.98));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  padding: 50px 40px;
  width: 100%;
  max-width: 450px;
  backdrop-filter: blur(20px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 2;
}

.loginHeader {
  text-align: center;
  margin-bottom: 40px;
}

.loginTitle {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.5rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.loginIcon {
  font-size: 2.2rem;
}

.loginSubtitle {
  color: #b3b3b3;
  font-size: 1.1rem;
  margin: 0;
}

.loginForm {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.formGroup label {
  color: #ff073a;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 10px;
}

.formGroup input {
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  color: white;
  font-size: 16px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.formGroup input:focus {
  outline: none;
  border-color: #ff073a;
  box-shadow: 0 0 0 3px rgba(255, 7, 58, 0.2);
  background: rgba(255, 255, 255, 0.08);
}

.formGroup input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.loginButton {
  padding: 18px 30px;
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-top: 10px;
}

.loginButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
}

.loadingSpinner {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.errorMessage {
  background: linear-gradient(135deg, #dc3545, #c82333);
  color: white;
  padding: 15px 20px;
  border-radius: 12px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
  text-align: center;
}

.loginFooter {
  margin-top: 30px;
  text-align: center;
}

.securityNotice {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin: 0;
}

/* Responsive */
@media (max-width: 768px) {
  .loginCard {
    padding: 40px 30px;
    margin: 20px;
  }
  
  .loginTitle {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .loginCard {
    padding: 30px 25px;
  }
  
  .loginTitle {
    font-size: 1.8rem;
  }
  
  .loginIcon {
    font-size: 1.8rem;
  }
}