/* Gallery Page */
.gallery-page {
  min-height: 100vh;
  background: #0a0a12;
  position: relative;
}

/* Hero Section */
.gallery-hero {
  position: relative;
  height: 60vh;
  min-height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  overflow: hidden;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-glow-1 {
  position: absolute;
  top: 20%;
  left: 15%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.2) 0%, transparent 70%);
  filter: blur(50px);
}

.hero-glow-2 {
  position: absolute;
  bottom: 20%;
  right: 15%;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.15) 0%, transparent 70%);
  filter: blur(40px);
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: #ff073a;
  border-radius: 50%;
  bottom: 0;
}

.hero-content {
  text-align: center;
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 20px;
}

.hero-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 4.5rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 20px;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.gradient-text {
  background: linear-gradient(45deg, #ff073a, #8a2be2, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 3s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.hero-subtitle {
  font-size: 1.3rem;
  color: #b3b3b3;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto 40px;
  font-weight: 300;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 60px;
}

.stat {
  text-align: center;
}

.stat-number {
  font-size: 2.2rem;
  font-weight: 800;
  color: #ff073a;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
}

/* Gallery Content */
.gallery-content {
  padding: 100px 0;
  background: #0a0a12;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Filters */
.gallery-filters {
  margin-bottom: 60px;
}

.filters-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  max-width: 800px;
  margin: 0 auto;
}

.filter-btn {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #b3b3b3;
  padding: 12px 24px;
  border-radius: 25px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.filter-btn.active {
  background: linear-gradient(135deg, #ff073a, #8a2be2);
  color: white;
  border-color: transparent;
  box-shadow: 0 5px 15px rgba(255, 7, 58, 0.3);
}

.filter-count {
  background: rgba(255, 255, 255, 0.1);
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 0.8rem;
}

/* Gallery Grid */
.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.gallery-item {
  border-radius: 20px;
  overflow: hidden;
}

.item-card {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.item-image {
  position: relative;
  height: 300px;
  overflow: hidden;
}

.item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.item-card:hover .item-image img {
  transform: scale(1.1);
}

.image-placeholder {
  display: none;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2a2a35, #1a1a24);
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}

.image-placeholder i {
  font-size: 3rem;
  color: #ff073a;
  opacity: 0.5;
}

/* Item Overlay */
.item-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(10, 10, 18, 0.8) 100%
  );
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 25px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.item-card:hover .item-overlay {
  opacity: 1;
}

.overlay-content {
  margin-top: auto;
}

.item-title {
  color: #ffffff;
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 10px;
  line-height: 1.3;
}

.item-meta {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.item-date,
.item-location {
  color: #b3b3b3;
  font-size: 0.9rem;
}

.view-button {
  position: absolute;
  top: 25px;
  right: 25px;
  width: 45px;
  height: 45px;
  background: rgba(255, 255, 255, 0.9);
  color: #ff073a;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.1rem;
  backdrop-filter: blur(10px);
}

/* Category Badges */
.category-badge {
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  backdrop-filter: blur(10px);
}

.category-badge.events {
  background: rgba(255, 7, 58, 0.9);
  color: white;
}

.category-badge.djs {
  background: rgba(138, 43, 226, 0.9);
  color: white;
}

.category-badge.crowd {
  background: rgba(0, 255, 255, 0.9);
  color: #0a0a12;
}

.category-badge.backstage {
  background: rgba(255, 255, 255, 0.9);
  color: #0a0a12;
}

/* Load More */
.load-more-section {
  text-align: center;
}

.load-more-btn {
  background: linear-gradient(135deg, #ff073a, #8a2be2);
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 25px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(255, 7, 58, 0.3);
}

/* Lightbox */
.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 10, 18, 0.95);
  backdrop-filter: blur(20px);
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
}

.lightbox-content {
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  background: #1a1a24;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.lightbox-close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  z-index: 10;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.lightbox-close:hover {
  background: rgba(255, 7, 58, 0.8);
}

.lightbox-nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  z-index: 10;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.lightbox-prev {
  left: 20px;
}

.lightbox-next {
  right: 20px;
}

.lightbox-nav:hover {
  background: rgba(255, 7, 58, 0.8);
}

.lightbox-image {
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lightbox-image img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.lightbox-info {
  padding: 30px;
  background: #1a1a24;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.lightbox-info h2 {
  color: #ffffff;
  font-size: 1.8rem;
  margin-bottom: 15px;
}

.lightbox-meta {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.lightbox-meta span {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #b3b3b3;
  font-size: 0.9rem;
}

.lightbox-meta i {
  color: #ff073a;
}

.lightbox-category {
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.lightbox-category.events {
  background: rgba(255, 7, 58, 0.2);
  color: #ff073a;
}

.lightbox-category.djs {
  background: rgba(138, 43, 226, 0.2);
  color: #8a2be2;
}

.lightbox-category.crowd {
  background: rgba(0, 255, 255, 0.2);
  color: #00ffff;
}

.lightbox-category.backstage {
  background: rgba(255, 255, 255, 0.2);
  color: #ffffff;
}

/* Navigation */
.gallery-nav {
  position: fixed;
  top: 30px;
  left: 30px;
  z-index: 100;
}

.back-to-home {
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  padding: 12px 20px;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.back-to-home:hover {
  background: rgba(255, 7, 58, 0.8);
  border-color: transparent;
}

/* Responsive */
@media (max-width: 1024px) {
  .gallery-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
  }
  
  .hero-title {
    font-size: 3.5rem;
  }
}

@media (max-width: 768px) {
  .gallery-hero {
    height: 50vh;
    min-height: 400px;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
  }
  
  .hero-stats {
    gap: 30px;
  }
  
  .stat-number {
    font-size: 1.8rem;
  }
  
  .filters-container {
    gap: 10px;
  }
  
  .filter-btn {
    padding: 10px 16px;
    font-size: 0.8rem;
  }
  
  .gallery-nav {
    top: 20px;
    left: 20px;
  }
  
  .lightbox-overlay {
    padding: 20px;
  }
  
  .lightbox-content {
    max-width: 95vw;
    max-height: 95vh;
  }
  
  .lightbox-image {
    height: 60vh;
  }
}

@media (max-width: 480px) {
  .gallery-grid {
    grid-template-columns: 1fr;
  }
  
  .hero-title {
    font-size: 2rem;
  }
  
  .filters-container {
    flex-direction: column;
    align-items: center;
  }
  
  .filter-btn {
    width: 200px;
    justify-content: center;
  }
  
  .gallery-nav {
    top: 15px;
    left: 15px;
  }
  
  .back-to-home {
    padding: 10px 15px;
    font-size: 0.8rem;
  }
}