/* Header Principal */
.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(10, 10, 18, 0.95);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  z-index: 1000;
  transition: all 0.3s ease;
}

.main-header.scrolled {
  background: rgba(10, 10, 18, 0.98);
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.3);
}

.header-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
}

/* Logo Section */
.logo-section {
  display: flex;
  align-items: center;
  gap: 15px;
  cursor: pointer;
}

.logo-image {
  height: 50px;
  width: auto;
  filter: 
    brightness(0) invert(1)
    drop-shadow(0 0 10px rgba(255, 7, 58, 0.6));
  transition: all 0.3s ease;
}

.logo-section:hover .logo-image {
  filter: 
    brightness(0) invert(1)
    drop-shadow(0 0 20px rgba(255, 7, 58, 0.8));
}

.logo-text {
  display: flex;
  flex-direction: column;
  line-height: 1;
}

.logo-main {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.8rem;
  font-weight: 800;
  color: #ffffff;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.logo-subtitle {
  font-size: 0.7rem;
  color: #ff073a;
  font-weight: 600;
  letter-spacing: 3px;
  text-transform: uppercase;
}

.logo-fallback {
  font-family: 'Exo 2', sans-serif;
  font-size: 2rem;
  font-weight: 800;
  color: #ffffff;
  letter-spacing: 2px;
  text-transform: uppercase;
}

/* Desktop Navigation */
.desktop-nav ul {
  display: flex;
  list-style: none;
  gap: 40px;
  margin: 0;
  padding: 0;
}

.nav-link {
  background: none;
  border: none;
  color: #b3b3b3;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  padding: 10px 0;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.nav-line {
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #ff073a, #ff557f);
  border-radius: 2px;
  transform-origin: center;
}

/* Mobile Menu Button */
.mobile-menu-btn {
  display: none;
  flex-direction: column;
  gap: 4px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
}

.mobile-menu-btn span {
  width: 25px;
  height: 2px;
  background: #ffffff;
  transition: all 0.3s ease;
  transform-origin: center;
}

.mobile-menu-btn.active span:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}

.mobile-menu-btn.active span:nth-child(2) {
  opacity: 0;
}

.mobile-menu-btn.active span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
}

/* Mobile Menu */
.mobile-menu {
  position: fixed;
  top: 80px;
  left: 0;
  width: 100%;
  height: calc(100vh - 80px);
  background: rgba(10, 10, 18, 0.98);
  backdrop-filter: blur(20px);
  z-index: 999;
}

.mobile-menu-content {
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.mobile-nav-link {
  background: none;
  border: none;
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 15px 0;
  text-align: left;
  cursor: pointer;
  transition: all 0.3s ease;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

/* Hero Section */
.hero-section {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  overflow: hidden;
  margin-top: 80px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.hero-content {
  text-align: center;
  max-width: 800px;
  padding: 0 20px;
  position: relative;
  z-index: 2;
}

.hero-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 4rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 20px;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.accent-text {
  background: linear-gradient(45deg, #ff073a, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-subtitle {
  font-size: 1.3rem;
  color: #b3b3b3;
  margin-bottom: 50px;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-weight: 400;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 60px;
  margin-bottom: 50px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 800;
  color: #ff073a;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

.cta-button {
  background: linear-gradient(45deg, #ff073a, #ff557f);
  color: white;
  border: none;
  padding: 18px 40px;
  font-size: 1.1rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: all 0.3s ease;
  box-shadow: 0 5px 20px rgba(255, 7, 58, 0.3);
}

.arrow {
  font-size: 1.5rem;
  font-weight: 300;
}

/* Hero Background */
.hero-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.1) 0%, transparent 70%);
  filter: blur(40px);
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-particle {
  position: absolute;
  width: 2px;
  height: 2px;
  background: #ff073a;
  border-radius: 50%;
  bottom: 0;
}

/* Header Background Elements */
.header-bg-elements {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.bg-gradient-1 {
  position: absolute;
  top: -100px;
  right: -100px;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.1) 0%, transparent 70%);
  filter: blur(30px);
}

.bg-gradient-2 {
  position: absolute;
  bottom: -100px;
  left: -100px;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.1) 0%, transparent 70%);
  filter: blur(30px);
}

.bg-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.02) 0%, transparent 50%);
}

/* Responsive */
@media (max-width: 1024px) {
  .hero-title {
    font-size: 3rem;
  }
  
  .hero-stats {
    gap: 40px;
  }
}

@media (max-width: 768px) {
  .header-container {
    padding: 0 20px;
    height: 70px;
  }
  
  .desktop-nav {
    display: none;
  }
  
  .mobile-menu-btn {
    display: flex;
  }
  
  .logo-image {
    height: 40px;
  }
  
  .logo-main {
    font-size: 1.4rem;
  }
  
  .hero-section {
    margin-top: 70px;
    min-height: 80vh;
  }
  
  .hero-title {
    font-size: 2.2rem;
    letter-spacing: 2px;
  }
  
  .hero-subtitle {
    font-size: 1rem;
    letter-spacing: 2px;
  }
  
  .hero-stats {
    gap: 30px;
    flex-direction: column;
    align-items: center;
  }
  
  .stat-number {
    font-size: 2rem;
  }
  
  .cta-button {
    padding: 15px 30px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 1.8rem;
  }
  
  .logo-text {
    display: none;
  }
  
  .mobile-menu-content {
    padding: 20px;
  }
}