/* Loading Overlay */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

/* Background Elements */
.loading-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.loading-glow-1 {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.2) 0%, transparent 70%);
  filter: blur(60px);
  animation: float-glow 6s ease-in-out infinite;
}

.loading-glow-2 {
  position: absolute;
  bottom: 20%;
  right: 10%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.15) 0%, transparent 70%);
  filter: blur(50px);
  animation: float-glow 8s ease-in-out infinite reverse;
}

@keyframes float-glow {
  0%, 100% { transform: translateY(0px) scale(1); }
  50% { transform: translateY(-20px) scale(1.1); }
}

.loading-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.loading-particle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: #ff073a;
  border-radius: 50%;
  bottom: 0;
}

/* Main Content */
.loading-content {
  text-align: center;
  position: relative;
  z-index: 2;
  max-width: 600px;
  padding: 0 20px;
}

/* Logo Animation */
.logo-container {
  position: relative;
  display: inline-block;
  margin-bottom: 40px;
}

.logo-orb {
  position: relative;
  width: 120px;
  height: 120px;
  margin: 0 auto;
}

.logo-ring {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 2px solid transparent;
}

.logo-ring-1 {
  border-top: 2px solid #ff073a;
  border-right: 2px solid transparent;
  animation: spin-ring 3s linear infinite;
}

.logo-ring-2 {
  border-bottom: 2px solid #ff073a;
  border-left: 2px solid transparent;
  animation: spin-ring 4s linear infinite reverse;
}

.logo-ring-3 {
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: spin-ring 5s linear infinite;
}

@keyframes spin-ring {
  0% { transform: rotate(0deg) scale(1); }
  50% { transform: rotate(180deg) scale(1.1); }
  100% { transform: rotate(360deg) scale(1); }
}

.logo-core {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-image {
  width: 100%;
  height: 100%;
  filter: 
    brightness(0) invert(1)
    drop-shadow(0 0 10px rgba(255, 7, 58, 0.8));
}

.logo-fallback {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.5rem;
  font-weight: 800;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* Pulse Circles */
.pulse-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: 1px solid rgba(255, 7, 58, 0.3);
}

.pulse-1 {
  width: 140px;
  height: 140px;
}

.pulse-2 {
  width: 180px;
  height: 180px;
}

.pulse-3 {
  width: 220px;
  height: 220px;
}

/* Loading Text */
.loading-text {
  margin-bottom: 40px;
}

.loading-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.5rem;
  font-weight: 800;
  color: transparent;
  background: linear-gradient(45deg, #ff073a, #ff073a, #ff073a);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.loading-subtitle {
  color: #b3b3b3;
  font-size: 1.1rem;
  font-weight: 300;
  min-height: 24px;
}

/* Progress Bar */
.progress-container {
  width: 100%;
  max-width: 400px;
  height: 6px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  margin: 0 auto 15px;
  overflow: hidden;
  position: relative;
  transform-origin: left;
}

.progress-bar {
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #ff073a, #ff073a);
  border-radius: 3px;
  transform-origin: left;
  position: relative;
  overflow: hidden;
}

.progress-bar::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.progress-text {
  color: #b3b3b3;
  font-size: 0.9rem;
  font-weight: 600;
}

/* Frequency Visualizer */
.frequency-visualizer {
  display: flex;
  align-items: end;
  justify-content: center;
  gap: 2px;
  height: 60px;
  margin: 30px auto;
  max-width: 300px;
}

.frequency-bar {
  background: linear-gradient(to top, #ff073a, #ff073a);
  border-radius: 1px 1px 0 0;
  min-height: 10%;
}

/* Loading Tips */
.loading-tips {
  margin-top: 30px;
}

.loading-tips p {
  color: #666;
  font-size: 0.9rem;
  font-style: italic;
}

/* Decorative Corners */
.loading-corners {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.corner {
  position: absolute;
  width: 30px;
  height: 30px;
  border: 2px solid rgba(255, 7, 58, 0.3);
  opacity: 0;
  animation: fade-in-corners 2s ease-in-out forwards;
}

.corner-tl {
  top: 20px;
  left: 20px;
  border-right: none;
  border-bottom: none;
  animation-delay: 0.5s;
}

.corner-tr {
  top: 20px;
  right: 20px;
  border-left: none;
  border-bottom: none;
  animation-delay: 0.7s;
}

.corner-bl {
  bottom: 20px;
  left: 20px;
  border-right: none;
  border-top: none;
  animation-delay: 0.9s;
}

.corner-br {
  bottom: 20px;
  right: 20px;
  border-left: none;
  border-top: none;
  animation-delay: 1.1s;
}

@keyframes fade-in-corners {
  to {
    opacity: 1;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .loading-title {
    font-size: 2rem;
  }
  
  .loading-subtitle {
    font-size: 1rem;
  }
  
  .logo-orb {
    width: 100px;
    height: 100px;
  }
  
  .logo-core {
    width: 50px;
    height: 50px;
  }
  
  .frequency-visualizer {
    max-width: 250px;
    height: 50px;
  }
}

@media (max-width: 480px) {
  .loading-title {
    font-size: 1.6rem;
  }
  
  .logo-orb {
    width: 80px;
    height: 80px;
  }
  
  .logo-core {
    width: 40px;
    height: 40px;
  }
  
  .pulse-1 { width: 120px; height: 120px; }
  .pulse-2 { width: 150px; height: 150px; }
  .pulse-3 { width: 180px; height: 180px; }
}