/* Admin Events Panel - Estilo Premium */
.adminContainer {
  position: relative;
  background: #0a0a12;
  min-height: 100vh;
  overflow: hidden;
  padding: 0;
}

/* Hero Section del Admin */
.adminHero {
  position: relative;
  height: 40vh;
  min-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  overflow: hidden;
  margin: 20px;
  border-radius: 25px;
}

.adminHeroBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.adminHeroGlow1 {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.25) 0%, transparent 70%);
  filter: blur(60px);
}

.adminHeroGlow2 {
  position: absolute;
  bottom: 20%;
  right: 10%;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.2) 0%, transparent 70%);
  filter: blur(50px);
}

.adminHeroParticles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.adminHeroParticle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: #ff073a;
  border-radius: 50%;
  bottom: 0;
  animation: floatParticle 6s ease-in-out infinite;
}

@keyframes floatParticle {
  0%, 100% {
    transform: translateY(0) translateX(0);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  50% {
    transform: translateY(-80px) translateX(20px);
  }
}

.adminHeroContent {
  text-align: center;
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 20px;
}

.adminHeroTitle {
  font-family: 'Exo 2', sans-serif;
  font-size: 3.5rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 15px;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.adminGradientText {
  background: linear-gradient(45deg, #ff073a, #ff073a, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: adminGradientShift 3s ease infinite;
}

@keyframes adminGradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.adminHeroSubtitle {
  font-size: 1.2rem;
  color: #b3b3b3;
  line-height: 1.6;
  max-width: 500px;
  margin: 0 auto 30px;
  font-weight: 300;
}

.adminHeroStats {
  display: flex;
  justify-content: center;
  gap: 50px;
}

.adminStat {
  text-align: center;
}

.adminStatNumber {
  font-size: 2rem;
  font-weight: 800;
  color: #ff073a;
  margin-bottom: 5px;
}

.adminStatLabel {
  font-size: 0.85rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  font-weight: 600;
}

/* Main Content */
.adminMainContent {
  padding: 60px 20px;
  max-width: 1400px;
  margin: 0 auto;
}

/* Form Section - Estilo Premium */
.formSection {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border-radius: 25px;
  padding: 40px;
  margin-bottom: 50px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
}

.formSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 7, 58, 0.03) 0%, transparent 50%);
  pointer-events: none;
}

.formHeader {
  text-align: center;
  margin-bottom: 40px;
}

.formTitle {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.formSubtitle {
  font-size: 1.1rem;
  color: #b3b3b3;
  max-width: 500px;
  margin: 0 auto;
}

/* Form Grid Mejorado */
.formGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 25px;
  margin-bottom: 35px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  position: relative;
}

.fullWidth {
  grid-column: 1 / -1;
}

.formGroup label {
  margin-bottom: 12px;
  font-weight: 700;
  color: #ff073a;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 8px;
}

.formGroup input,
.formGroup textarea,
.formGroup select {
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  color: white;
  font-size: 15px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.formGroup input:focus,
.formGroup textarea:focus,
.formGroup select:focus {
  outline: none;
  border-color: #ff073a;
  box-shadow: 0 0 0 3px rgba(255, 7, 58, 0.2);
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

.formGroup input::placeholder,
.formGroup textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.formGroup textarea {
  resize: vertical;
  min-height: 120px;
  line-height: 1.5;
}

/* Date Inputs Mejorados */
.dateInputGroup {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.dateDisplayInput {
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  color: white;
  font-size: 15px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.dateDisplayInput:focus {
  outline: none;
  border-color: #ff073a;
  box-shadow: 0 0 0 3px rgba(255, 7, 58, 0.2);
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

.datePickerContainer {
  position: relative;
}

.datePickerInput {
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  color: white;
  font-size: 15px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  width: 100%;
  cursor: pointer;
}

.datePickerInput:focus {
  outline: none;
  border-color: #ff073a;
  box-shadow: 0 0 0 3px rgba(255, 7, 58, 0.2);
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

.timeInput {
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  color: white;
  font-size: 15px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  width: 100%;
}

.timeInput:focus {
  outline: none;
  border-color: #ff073a;
  box-shadow: 0 0 0 3px rgba(255, 7, 58, 0.2);
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

/* DJs Input con Tags */
.djsInputContainer {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  padding: 12px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.djsInputContainer:focus-within {
  border-color: #ff073a;
  box-shadow: 0 0 0 3px rgba(255, 7, 58, 0.2);
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

.djsTags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 10px;
}

.djTagInput {
  background: rgba(255, 7, 58, 0.15);
  color: #ff073a;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  border: 1px solid rgba(255, 7, 58, 0.3);
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
}

.djTagInput:hover {
  background: rgba(255, 7, 58, 0.25);
  transform: translateY(-1px);
}

.removeDjTag {
  background: none;
  border: none;
  color: #ff073a;
  cursor: pointer;
  font-size: 1rem;
  padding: 0;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.removeDjTag:hover {
  background: rgba(255, 7, 58, 0.3);
  transform: scale(1.1);
}

.djsInput {
  background: transparent;
  border: none;
  color: white;
  font-size: 15px;
  padding: 8px 4px;
  width: 100%;
  outline: none;
}

.djsInput::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.djsHelpText {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 8px;
  display: block;
}

/* Input de precios sin flechas */
.priceInput {
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  color: white;
  font-size: 15px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  -moz-appearance: textfield;
}

.priceInput::-webkit-outer-spin-button,
.priceInput::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.priceInput:focus {
  outline: none;
  border-color: #ff073a;
  box-shadow: 0 0 0 3px rgba(255, 7, 58, 0.2);
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

/* Image Preview Mejorado */
.imagePreview {
  text-align: center;
  margin-bottom: 30px;
  padding: 25px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 20px;
  border: 2px dashed rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.imagePreview:hover {
  border-color: rgba(255, 7, 58, 0.3);
  background: rgba(255, 255, 255, 0.03);
}

.previewImage {
  max-width: 350px;
  max-height: 200px;
  border-radius: 15px;
  border: 3px solid #ff073a;
  box-shadow: 0 10px 30px rgba(255, 7, 58, 0.3);
  object-fit: cover;
}

/* Submit Button Premium */
.submitBtn {
  width: 100%;
  padding: 18px 30px;
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 16px;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.4s ease;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(255, 7, 58, 0.4);
}

.submitBtn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.submitBtn:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(255, 7, 58, 0.6);
}

.submitBtn:hover:not(:disabled)::before {
  left: 100%;
}

.submitBtn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: 0 5px 15px rgba(255, 7, 58, 0.2);
}

/* Events List Section */
.eventsList {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border-radius: 25px;
  padding: 40px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
}

.eventsList::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(138, 43, 226, 0.03) 0%, transparent 50%);
  pointer-events: none;
}

.eventsHeader {
  text-align: center;
  margin-bottom: 40px;
}

.eventsTitle {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.eventsSubtitle {
  font-size: 1.1rem;
  color: #b3b3b3;
}

/* Event Items Premium */
.eventItem {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.02));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 25px;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.eventItem::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 7, 58, 0.05) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.4s ease;
  border-radius: 20px;
}

.eventItem:hover::before {
  opacity: 1;
}

.eventItem:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 7, 58, 0.3);
}

.eventHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 25px;
  position: relative;
  z-index: 2;
}

.eventTitle {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  flex: 1;
}

.deleteBtn {
  background: linear-gradient(135deg, #dc3545, #c82333);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 12px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  gap: 8px;
  box-shadow: 0 5px 15px rgba(220, 53, 69, 0.4);
}

.deleteBtn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(220, 53, 69, 0.6);
  background: linear-gradient(135deg, #c82333, #a71e2a);
}

.eventContent {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 30px;
  position: relative;
  z-index: 2;
}

.eventImageContainer {
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.eventImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.eventItem:hover .eventImage {
  transform: scale(1.05);
}

.eventDetails {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.eventDetail {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 12px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.eventDetail:last-child {
  border-bottom: none;
}

.eventDetail strong {
  color: #ff073a;
  font-weight: 700;
  min-width: 120px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.eventDetailContent {
  flex: 1;
  color: #e0e0e0;
  line-height: 1.5;
}

.djList {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 5px;
}

.djTag {
  background: rgba(255, 7, 58, 0.1);
  color: #ff073a;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  border: 1px solid rgba(255, 7, 58, 0.3);
}

.eventLinks {
  display: flex;
  gap: 15px;
  margin-top: 5px;
}

.eventLink {
  color: #ff073a;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 6px;
}

.eventLink:hover {
  color: #ff6b6b;
  transform: translateY(-1px);
}

/* No Events State */
.noEvents {
  text-align: center;
  padding: 80px 40px;
  color: #b3b3b3;
}

.noEventsIcon {
  font-size: 4rem;
  color: #8a2be2;
  margin-bottom: 20px;
  opacity: 0.7;
}

.noEvents h3 {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.8rem;
  color: #ffffff;
  margin-bottom: 15px;
}

.noEvents p {
  font-size: 1.1rem;
  max-width: 400px;
  margin: 0 auto;
  line-height: 1.6;
}

/* File Input Styling */
.fileInputWrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
  width: 100%;
}

.fileInputWrapper input[type="file"] {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.fileInputLabel {
  display: block;
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 2px dashed rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  color: rgba(255, 255, 255, 0.7);
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.fileInputLabel:hover {
  border-color: #ff073a;
  color: #ff073a;
  background: rgba(255, 7, 58, 0.05);
}

/* Loading States */
.loadingOverlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 10, 18, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  border-radius: 25px;
}

.loadingSpinner {
  font-size: 3rem;
  color: #ff073a;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .adminHeroTitle {
    font-size: 3rem;
  }
  
  .formGrid {
    gap: 20px;
  }
}

@media (max-width: 1024px) {
  .adminMainContent {
    padding: 40px 20px;
  }
  
  .eventContent {
    grid-template-columns: 250px 1fr;
    gap: 25px;
  }
}

@media (max-width: 768px) {
  .adminHero {
    height: 35vh;
    min-height: 250px;
    margin: 15px;
  }
  
  .adminHeroTitle {
    font-size: 2.2rem;
  }
  
  .adminHeroSubtitle {
    font-size: 1rem;
  }
  
  .adminHeroStats {
    gap: 30px;
  }
  
  .adminStatNumber {
    font-size: 1.6rem;
  }
  
  .formSection,
  .eventsList {
    padding: 30px 25px;
    margin: 15px;
  }
  
  .formGrid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .dateInputGroup {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .eventContent {
    grid-template-columns: 1fr;
  }
  
  .eventImageContainer {
    height: 250px;
  }
  
  .eventHeader {
    flex-direction: column;
    gap: 15px;
  }
  
  .eventTitle {
    font-size: 1.5rem;
    text-align: center;
  }
  
  .formTitle,
  .eventsTitle {
    font-size: 2rem;
  }
  
  .adminMainContent {
    padding: 30px 15px;
  }
}

@media (max-width: 480px) {
  .adminHeroTitle {
    font-size: 1.8rem;
  }
  
  .adminHeroStats {
    gap: 20px;
  }
  
  .adminStatNumber {
    font-size: 1.4rem;
  }
  
  .adminStatLabel {
    font-size: 0.75rem;
  }
  
  .formSection,
  .eventsList {
    padding: 25px 20px;
    margin: 10px;
  }
  
  .formTitle,
  .eventsTitle {
    font-size: 1.7rem;
  }
  
  .eventTitle {
    font-size: 1.3rem;
  }
  
  .eventDetail {
    flex-direction: column;
    gap: 8px;
  }
  
  .eventDetail strong {
    min-width: auto;
  }
  
  .eventLinks {
    flex-direction: column;
    gap: 10px;
  }
  
  .eventImageContainer {
    height: 200px;
  }
}

/* Header con usuario y logout */
.adminHeader {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 100;
}

.userInfo {
  display: flex;
  align-items: center;
  gap: 15px;
  color: white;
  font-size: 0.9rem;
}

.logoutBtn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 8px 15px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 5px;
}

.logoutBtn:hover {
  background: rgba(255, 7, 58, 0.2);
  border-color: rgba(255, 7, 58, 0.5);
}

/* Header mejorado */
.adminHeader {
  position: absolute;
  top: 25px;
  right: 25px;
  z-index: 100;
}

.userInfo {
  display: flex;
  align-items: center;
  gap: 20px;
}

.userBadge {
  display: flex;
  align-items: center;
