  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.1rem;
}

.modalCloseBtn:hover {
  background: rgba(255, 7, 58, 0.2);
  border-color: rgba(255, 7, 58, 0.5);
