/* Notifications */
.notification {
  position: fixed;
  top: 20px;
  right: 20px;
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 0;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
  z-index: 10001;
  overflow: hidden;
  min-width: 300px;
  max-width: 400px;
}

.notification.success {
  border-left: 4px solid #00d26a;
}

.notification.error {
  border-left: 4px solid #ff073a;
}

.notification.warning {
  border-left: 4px solid #ffb800;
}

.notificationContent {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 20px;
}

.notificationIcon {
  font-size: 1.5rem;
  flex-shrink: 0;
}

.notificationText {
  flex: 1;
}

.notificationTitle {
  display: block;
  font-weight: 700;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 4px;
}

.notification.success .notificationTitle {
  color: #00d26a;
}

.notification.error .notificationTitle {
  color: #ff073a;
}

.notification.warning .notificationTitle {
  color: #ffb800;
}

.notificationMessage {
  color: #e0e0e0;
  font-size: 0.9rem;
  margin: 0;
  line-height: 1.4;
}

.notificationClose {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  font-size: 1rem;
  padding: 5px;
  border-radius: 4px;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.notificationClose:hover {
  color: white;
  background: rgba(255, 255, 255, 0.1);
}

.notificationProgress {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, #ff073a, #8a2be2);
  transform-origin: left;
}

.notification.success .notificationProgress {
  background: linear-gradient(90deg, #00d26a, #00b359);
}

.notification.error .notificationProgress {
  background: linear-gradient(90deg, #ff073a, #e00632);
}

.notification.warning .notificationProgress {
  background: linear-gradient(90deg, #ffb800, #ff9900);
}

