:root {
  --primary: #ff073a;
  --primary-dark: #cc0030;
  --secondary: #0a0a12;
  --accent: #ff557f;
  --text: #ffffff;
  --text-secondary: #b3b3b3;
  --card-bg: rgba(20, 20, 30, 0.8);
  --card-border: rgba(255, 7, 58, 0.3);
  --gradient: linear-gradient(135deg, #ff073a, #ff557f);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--secondary);
  color: var(--text);
  font-family: 'Montserrat', sans-serif;
  line-height: 1.6;
  overflow-x: hidden;
  background-image: 
    linear-gradient(rgba(10, 10, 18, 0.95), rgba(10, 10, 18, 0.95));
}

.App {
  position: relative;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* ===== SCROLL PERSONALIZADO ===== */
/* Scrollbar global para toda la aplicación */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: rgba(10, 10, 18, 0.8);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, var(--primary), var(--accent));
  border-radius: 10px;
  border: 2px solid var(--secondary);
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, var(--primary-dark), #7b1fd6);
  box-shadow: 0 0 10px rgba(255, 7, 58, 0.4);
}

::-webkit-scrollbar-thumb:active {
  background: linear-gradient(135deg, #cc0030, #6a1cb3);
}

::-webkit-scrollbar-corner {
  background: var(--secondary);
}

/* Para Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: var(--primary) var(--secondary);
}

/* Scroll Personalizado para modales y contenedores específicos */
.modal-scroll-container {
  max-height: calc(90vh - 100px);
  overflow-y: auto;
  padding: 0 25px 25px 25px;
}

/* Scrollbar personalizado para modales */
.modal-scroll-container::-webkit-scrollbar {
  width: 8px;
}

.modal-scroll-container::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
}

.modal-scroll-container::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  border-radius: 10px;
}

.modal-scroll-container::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #ff073a, #ff073a);
}

/* Scroll para elementos con clase scroll-container */
.scroll-container {
  scrollbar-width: thin;
  scrollbar-color: var(--primary) rgba(255, 255, 255, 0.05);
}

.scroll-container::-webkit-scrollbar {
  width: 6px;
}

.scroll-container::-webkit-scrollbar-thumb {
  background: var(--primary);
  border-radius: 3px;
}

/* ===== FIN SCROLL PERSONALIZADO ===== */

/* Animaciones globales */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0px); }
}

@keyframes glow {
  0% { box-shadow: 0 0 20px rgba(255, 7, 58, 0.4); }
  50% { box-shadow: 0 0 30px rgba(255, 7, 58, 0.8); }
  100% { box-shadow: 0 0 20px rgba(255, 7, 58, 0.4); }
}

/* Scroll animations */
.scroll-animate {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s ease;
}

.scroll-animate.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Secciones */
section {
  padding: 100px 0;
  position: relative;
}

.section-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.8rem;
  color: var(--text);
  text-align: center;
  margin-bottom: 60px;
  text-transform: uppercase;
  letter-spacing: 5px;
  position: relative;
  padding-bottom: 20px;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 4px;
  background: var(--gradient);
}

/* Efectos de partículas */
.particles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.particle {
  position: absolute;
  background: var(--primary);
  border-radius: 50%;
  opacity: 0.3;
  animation: float 15s infinite ease-in-out;
}

/* Responsive */
@media (max-width: 768px) {
  .section-title {
    font-size: 2.2rem;
  }
  
  section {
    padding: 60px 0;
  }
  
  /* Scrollbar más delgada en móviles */
  ::-webkit-scrollbar {
    width: 6px;
  }
  
  .modal-scroll-container::-webkit-scrollbar {
    width: 4px;
  }
}