/* Events Section */
.events-section {
  position: relative;
  background: #0a0a12;
  overflow: hidden;
}

/* Hero Section */
.events-hero {
  position: relative;
  height: 60vh;
  min-height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  overflow: hidden;
  border-radius: 20px;
  margin: 20px;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-glow-1 {
  position: absolute;
  top: 20%;
  left: 15%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.2) 0%, transparent 70%);
  filter: blur(50px);
}

.hero-glow-2 {
  position: absolute;
  bottom: 20%;
  right: 15%;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.15) 0%, transparent 70%);
  filter: blur(40px);
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: #ff073a;
  border-radius: 50%;
  bottom: 0;
}

.hero-content {
  text-align: center;
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 20px;
}

.hero-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 4.5rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 20px;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.gradient-text {
  background: linear-gradient(45deg, #ff073a, #ff073a, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 3s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.hero-subtitle {
  font-size: 1.3rem;
  color: #b3b3b3;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto 40px;
  font-weight: 300;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 60px;
}

.stat {
  text-align: center;
}

.stat-number {
  font-size: 2.2rem;
  font-weight: 800;
  color: #ff073a;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
}

/* Events Container */
.events-container {
  padding: 80px 0;
  background: #0a0a12;
}


.carousel-section {
  text-align: center;
  margin-bottom: 80px;
}

.carousel-header {
  margin-bottom: 60px;
}

.carousel-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 3rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.carousel-subtitle {
  font-size: 1.2rem;
  color: #b3b3b3;
  max-width: 500px;
  margin: 0 auto;
}

.dual-carousel {
  position: relative;
  margin: 0 auto;
  max-width: 1200px;
}

/* CAROUSEL TRACK MEJORADO */
.carousel-track {
  position: relative;
  overflow: hidden;
  margin-bottom: 50px;
}

/* GRID CON ALTURAS SINCRONIZADAS */
.carousel-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  position: relative;
  width: 100%;
  align-items: stretch; 
}

/* Event Card - MEJORADA PARA ALTURAS SINCRONIZADAS */
.event-card {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  overflow: hidden;
  transition: all 0.4s ease;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  position: relative;
  cursor: pointer;
  height: 100%; /* Ocupa el 100% de la altura del grid */
  display: flex;
  flex-direction: column;
  min-height: 550px; /* Altura mínima base */
}

.event-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 7, 58, 0.05) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.4s ease;
  border-radius: 25px;
}

.event-card:hover::before {
  opacity: 1;
}

.event-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.4);
}

/* Event Header */
.event-header {
  position: relative;
  overflow: hidden;
  flex-shrink: 0; /* No se encoge */
}

.event-flyer {
  height: 300px;
  overflow: hidden;
  position: relative;
}

.event-flyer img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.event-card:hover .event-flyer img {
  transform: scale(1.05);
}

.event-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, transparent, rgba(10, 10, 18, 0.3));
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 25px;
}

.event-date {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
  padding: 15px 20px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(255, 7, 58, 0.4);
  min-width: 80px;
}

.date-day {
  display: block;
  font-size: 1.8rem;
  font-weight: 800;
  line-height: 1;
}

.date-month {
  display: block;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 5px;
}

.event-badge {
  background: rgba(255, 255, 255, 0.95);
  color: #ff073a;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  gap: 8px;
  backdrop-filter: blur(10px);
}

/* Event Content - MEJORADO PARA ALTURAS SINCRONIZADAS */
.event-content {
  padding: 30px;
  display: flex;
  flex-direction: column;
  flex: 1; /* Ocupa todo el espacio disponible */
  min-height: 250px;
}

.event-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.6rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 15px;
  line-height: 1.3;
  flex-shrink: 0;
}

.event-location {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ff073a;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 20px;
  flex-shrink: 0;
}

.event-location i {
  font-size: 1.1rem;
}

/* DJs Section - EXPANSIÓN AUTOMÁTICA CONTROLADA */
.djs-section {
  margin-bottom: 25px;
  flex: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
}

.djs-title {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 15px;
  flex-shrink: 0;
}

.djs-title i {
  color: #ff073a;
}

.djs-lineup {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  flex: 1;
  min-height: 0;
  align-content: flex-start;
}

.dj-tag {
  background: rgba(255, 7, 58, 0.1);
  color: #ff073a;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  border: 1px solid rgba(255, 7, 58, 0.3);
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.dj-tag:hover {
  background: rgba(255, 7, 58, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 7, 58, 0.2);
}

.more-djs {
  background: rgba(138, 43, 226, 0.1) !important;
  color: #8a2be2 !important;
  border-color: rgba(138, 43, 226, 0.3) !important;
}

/* Event Actions - SIEMPRE AL FINAL */
.event-actions {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 15px;
  flex-shrink: 0;
  margin-top: auto; /* Empuja los botones hacia abajo */
  padding-top: 20px;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 15px 20px;
  border-radius: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.btn-primary {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-outline {
  background: transparent;
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.btn-instagram {
  background: linear-gradient(45deg, #E1306C, #F77737);
  color: white;
}

/* Controles del carrusel */
.carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 20;
  transition: all 0.3s ease;
  font-size: 1.2rem;
}

.carousel-control:hover {
  background: rgba(255, 7, 58, 0.3);
  border-color: rgba(255, 7, 58, 0.5);
  transform: translateY(-50%) scale(1.1);
}

.carousel-control.prev {
  left: -80px;
}

.carousel-control.next {
  right: -80px;
}

/* Indicadores con tiempo de 10 segundos */
.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 30px 0;
}

.indicator {
  width: 60px;
  height: 4px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 2px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.indicator.active {
  background: rgba(255, 7, 58, 0.3);
}

.indicator-progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background: #ff073a;
  border-radius: 2px;
  transition: width 10s linear;
}

.indicator.active .indicator-progress {
  width: 100%;
  animation: progress 10s linear;
}

@keyframes progress {
  0% { width: 0%; }
  100% { width: 100%; }
}

/* Contador */
.carousel-counter {
  display: flex;
  align-items: baseline;
  gap: 5px;
  font-weight: 700;
  justify-content: center;
  margin: 10px 0;
}

.current-number {
  font-size: 1.5rem;
  color: #ff073a;
}

.total-number {
  font-size: 1rem;
  color: #b3b3b3;
}

/* Events CTA */
.events-cta {
  text-align: center;
  padding: 60px 0;
  background: linear-gradient(135deg, #1a1a24, #161622);
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: 40px;
}

.cta-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 15px;
}

.cta-subtitle {
  font-size: 1.1rem;
  color: #b3b3b3;
  margin-bottom: 40px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.cta-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

/* Modal Styles */
.event-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 10, 18, 0.95);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.event-modal {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  overflow: hidden;
  position: relative;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.5);
}

.modal-header {
  padding: 25px 25px 0 25px;
}

.modal-header-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.modal-event-date {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
  padding: 15px 20px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(255, 7, 58, 0.4);
  min-width: 80px;
}

.modal-date-day {
  display: block;
  font-size: 1.8rem;
  font-weight: 800;
  line-height: 1;
}

.modal-date-month {
  display: block;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 5px;
}

.modal-close-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.modal-close-btn:hover {
  background: rgba(255, 7, 58, 0.2);
  border-color: rgba(255, 7, 58, 0.5);
}

/* Scroll Personalizado */
.modal-scroll-container {
  max-height: calc(90vh - 100px);
  overflow-y: auto;
  padding: 0 25px 25px 25px;
}

/* Scrollbar personalizado */
.modal-scroll-container::-webkit-scrollbar {
  width: 8px;
}

.modal-scroll-container::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
}

.modal-scroll-container::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  border-radius: 10px;
}

.modal-scroll-container::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #ff073a, #ff073a);
}

.modal-content {
  padding-top: 0;
}

.modal-flyer-section {
  margin: 25px 0;
}

.modal-flyer {
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
}

.modal-flyer img {
  width: 100%;
  height: auto;
  display: block;
}

.modal-main-info {
  text-align: center;
  margin-bottom: 30px;
}

.modal-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.2rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 15px;
  line-height: 1.3;
}

.modal-location {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: #ff073a;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.modal-badge {
  background: rgba(255, 255, 255, 0.95);
  color: #ff073a;
  padding: 12px 20px;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  backdrop-filter: blur(10px);
}

.modal-section {
  margin-bottom: 35px;
  padding: 25px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.modal-section-title {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ffffff;
  font-size: 1.1rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 20px;
}

.modal-section-title i {
  color: #ff073a;
}

.modal-djs-lineup {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.modal-dj-tag {
  background: rgba(255, 7, 58, 0.1);
  color: #ff073a;
  padding: 12px 20px;
  border-radius: 25px;
  font-size: 0.95rem;
  font-weight: 600;
  border: 1px solid rgba(255, 7, 58, 0.3);
}

.modal-info-text {
  color: #b3b3b3;
  line-height: 1.7;
}

.info-line {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 12px;
  padding: 8px 0;
}

.info-line i {
  color: #ff073a;
  margin-top: 2px;
  flex-shrink: 0;
}

.info-line span {
  flex: 1;
}

.modal-pricing-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.modal-price-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 25px 20px;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
}

.modal-price-item.highlight {
  background: rgba(255, 7, 58, 0.1);
  border-color: rgba(255, 7, 58, 0.3);
  transform: scale(1.05);
}

.price-icon {
  width: 50px;
  height: 50px;
  background: rgba(255, 7, 58, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 15px;
  font-size: 1.2rem;
  color: #ff073a;
}

.modal-price-type {
  display: block;
  font-size: 0.9rem;
  color: #b3b3b3;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.modal-price-amount {
  display: block;
  font-size: 1.6rem;
  font-weight: 800;
  color: #ffffff;
}

.modal-countdown {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  padding: 25px;
}

.countdown-item {
  text-align: center;
}

.countdown-number {
  display: block;
  font-size: 2.5rem;
  font-weight: 800;
  color: #ff073a;
  line-height: 1;
}

.countdown-label {
  display: block;
  font-size: 0.8rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 8px;
}

.countdown-separator {
  color: #ff073a;
  font-size: 2rem;
  font-weight: 700;
  margin-top: -10px;
}

.modal-actions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-top: 10px;
}

.modal-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 18px 25px;
  border-radius: 15px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1rem;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Responsive */
@media (max-width: 1200px) {
  .carousel-control.prev {
    left: -40px;
  }
  
  .carousel-control.next {
    right: -40px;
  }
}

@media (max-width: 1024px) {
  .hero-title {
    font-size: 3.5rem;
  }
  
  .carousel-grid {
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .events-hero {
    height: 50vh;
    min-height: 400px;
    margin: 15px;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
  }
  
  .hero-stats {
    gap: 30px;
  }
  
  .stat-number {
    font-size: 1.8rem;
  }
  
  /* En móvil: 1 evento por slide - ALTURAS INDEPENDIENTES */
  .carousel-grid {
    grid-template-columns: 1fr;
    gap: 30px;
    align-items: start; /* En móvil no necesitan misma altura */
  }
  
  .carousel-track {
    margin-bottom: 40px;
  }
  
  .carousel-title {
    font-size: 2.2rem;
  }
  
  .carousel-control {
    width: 50px;
    height: 50px;
    font-size: 1rem;
  }
  
  .carousel-control.prev {
    left: -20px;
  }
  
  .carousel-control.next {
    right: -20px;
  }
  
  .event-actions {
    grid-template-columns: 1fr;
  }
  
  .event-flyer {
    height: 250px;
  }
  
  .event-card {
    min-height: 500px;
    height: auto; /* En móvil, altura automática */
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .cta-title {
    font-size: 2rem;
  }
  
  .carousel-indicators {
    margin: 25px 0;
  }
  
  .modal-actions {
    grid-template-columns: 1fr;
  }
  
  .modal-pricing-grid {
    grid-template-columns: 1fr;
  }
  
  .modal-price-item.highlight {
    transform: none;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .event-flyer {
    height: 220px;
  }
  
  .event-overlay {
    padding: 15px;
  }
  
  .event-date {
    padding: 12px 15px;
