/* Estados de carga y error */
.loading-container,
.error-container,
.no-events-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  min-height: 50vh;
}

.loading-spinner {
  font-size: 3rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.error-container i,
.no-events-container i {
  font-size: 4rem;
  color: #ff6b6b;
  margin-bottom: 1rem;
}

.no-events-container i {
  color: #a5b4fc;
}

.error-container h3,
.no-events-container h3 {
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
}

.error-container p,
.no-events-container p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  max-width: 400px;
}

/* Countdown Styles */
.modal-countdown {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: linear-gradient(135deg, var(--card-bg), rgba(255, 255, 255, 0.05));
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.countdown-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 60px;
}

.countdown-number {
  font-size: 2rem;
  font-weight: 700;
  background: linear-gradient(135deg, #ff073a, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
}

.countdown-label {
  font-size: 0.75rem;
  color: #b3b3b3;
  margin-top: 0.25rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.countdown-separator {
  font-size: 1.5rem;
  font-weight: 700;
  color: #ff073a;
  margin-top: -0.5rem;
}

.event-in-progress {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
  background: linear-gradient(135deg, #00b894, #00a085);
  border-radius: 12px;
  color: white;
  font-weight: 600;
}

.event-in-progress i {
  font-size: 1.5rem;
}

/* Badge dinámico */
.modal-badge.past {
  background: linear-gradient(135deg, #636e72, #2d3436);
}

/* Responsive para countdown */
@media (max-width: 768px) {
  .modal-countdown {
    gap: 0.5rem;
    padding: 1rem;
  }
  
  .countdown-number {
    font-size: 1.5rem;
  }
  
  .countdown-item {
    min-width: 50px;
  }
  
  .countdown-separator {
    font-size: 1.25rem;
  }
  
  .countdown-label {
    font-size: 0.7rem;
  }
}

/* Estados del Countdown */
.event-finished {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
  background: linear-gradient(135deg, #dc3545, #c82333);
  border-radius: 12px;
  color: white;
  font-weight: 600;
  text-align: center;
}

.event-finished i {
  font-size: 1.5rem;
}

.event-in-progress {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1.5rem;
  background: linear-gradient(135deg, #00b894, #00a085);
  border-radius: 12px;
  color: white;
  font-weight: 600;
  margin-bottom: 1rem;
  text-align: center;
}

.event-in-progress i {
  font-size: 1.5rem;
}

/* Countdown para evento en curso */
.modal-countdown.ongoing {
  background: linear-gradient(135deg, rgba(0, 184, 148, 0.1), rgba(0, 160, 133, 0.05));
  border: 1px solid rgba(0, 184, 148, 0.3);
}

.modal-countdown.ongoing .countdown-number {
  background: linear-gradient(135deg, #00b894, #00a085);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Badges dinámicos en EventCard */
.event-badge.finished {
  background: linear-gradient(135deg, #dc3545, #c82333);
}

.event-badge.ongoing {
  background: linear-gradient(135deg, #00b894, #00a085);
}

.event-badge.upcoming {
  background: rgba(255, 255, 255, 0.95);
  color: #ff073a;
}

/* En el modal también */
.modal-badge.finished {
  background: linear-gradient(135deg, #dc3545, #c82333);
  color: white;
}

.modal-badge.ongoing {
  background: linear-gradient(135deg, #00b894, #00a085);
  color: white;
}

.modal-badge.upcoming {
  background: rgba(255, 255, 255, 0.95);
  color: #ff073a;
}


/* Badges dinámicos para EventCard */
.event-badge.finished {
