/* History Section */
.history-section {
  position: relative;
  background: #0a0a12;
  overflow: hidden;
}

/* Hero Section */
.history-hero {
  position: relative;
  height: 70vh;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  overflow: hidden;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-glow-1 {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.15) 0%, transparent 70%);
  filter: blur(60px);
}

.hero-glow-2 {
  position: absolute;
  bottom: 20%;
  right: 10%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.1) 0%, transparent 70%);
  filter: blur(50px);
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-particle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: #ff073a;
  border-radius: 50%;
  bottom: 0;
}

.hero-content {
  text-align: center;
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 20px;
}

.hero-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 5rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 20px;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.gradient-text {
  background: linear-gradient(45deg, #ff073a, #ff073a, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 3s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.hero-subtitle {
  font-size: 1.4rem;
  color: #b3b3b3;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
  font-weight: 300;
}

/* Stats Section */
.history-stats {
  padding: 80px 0;
  background: rgba(255, 255, 255, 0.02);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.stats-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

.stat-card {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 40px 30px;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.05), transparent);
  transition: left 0.6s ease;
}

.stat-card:hover::before {
  left: 100%;
}

.stat-icon {
  width: 70px;
  height: 70px;
  background: linear-gradient(135deg, #ff073a, #ff073a);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  font-size: 1.8rem;
  color: white;
}

.stat-number {
  font-size: 3rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 10px;
  background: linear-gradient(45deg, #ffffff, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-label {
  font-size: 1rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
}

/* Story Section */
.story-section {
  padding: 120px 0;
  background: #0a0a12;
}

.story-content {
  max-width: 900px;
  margin: 0 auto;
}

.story-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 3.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 50px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.story-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #b3b3b3;
}

.story-text p {
  margin-bottom: 30px;
}

.highlight {
  color: #ff073a;
  font-weight: 600;
}

/* Timeline Section */
.timeline-section {
  padding: 120px 0;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 100%);
  position: relative;
  overflow: hidden;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.timeline-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255, 7, 58, 0.5), transparent);
}

.timeline-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 3rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.timeline-subtitle {
  font-size: 1.2rem;
  color: #b3b3b3;
  text-align: center;
  margin-bottom: 80px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.timeline-container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.timeline {
  position: relative;
}

.timeline::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: 100%;
  background: linear-gradient(to bottom, transparent, #ff073a, #ff073a, transparent);
}

.timeline-event {
  position: relative;
  margin-bottom: 100px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.timeline-event:nth-child(odd) .timeline-card {
  flex-direction: row;
}

.timeline-event:nth-child(even) .timeline-card {
  flex-direction: row-reverse;
}

.timeline-card {
  display: flex;
  align-items: center;
  gap: 40px;
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  padding: 40px;
  max-width: 900px;
  width: 100%;
  position: relative;
  transition: all 0.4s ease;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.timeline-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  flex-shrink: 0;
}

.timeline-year {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
  padding: 20px 30px;
  border-radius: 20px;
  font-size: 1.8rem;
  font-weight: 800;
  text-align: center;
  min-width: 140px;
  box-shadow: 0 10px 30px rgba(255, 7, 58, 0.3);
}

.timeline-connector {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.connector-line {
  width: 2px;
  height: 40px;
  background: linear-gradient(to bottom, #ff073a, #ff073a);
}

.connector-dot {
  width: 16px;
  height: 16px;
  background: #ff073a;
  border-radius: 50%;
  border: 4px solid #1a1a24;
  box-shadow: 0 0 0 4px #ff073a;
}

.timeline-content {
  flex: 1;
}

.timeline-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 15px;
  text-align: left;
}

.timeline-description {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #b3b3b3;
  margin-bottom: 20px;
}

.milestone-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 7, 58, 0.1);
  color: #ff073a;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  border: 1px solid rgba(255, 7, 58, 0.3);
}

.timeline-decoration {
  position: absolute;
  top: -20px;
  right: -20px;
}

.decoration-circle {
  width: 40px;
  height: 40px;
  border: 2px solid rgba(255, 7, 58, 0.3);
  border-radius: 50%;
  animation: rotate 10s linear infinite;
}

.decoration-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background: rgba(255, 7, 58, 0.1);
  border-radius: 50%;
  filter: blur(10px);
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Legacy Section */
.legacy-section {
  padding: 120px 0;
  background: #0a0a12;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.legacy-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.legacy-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 3rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 50px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.legacy-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #b3b3b3;
  text-align: left;
}

.legacy-text p {
  margin-bottom: 30px;
}

.legacy-quote {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 40px;
  margin-top: 40px;
  position: relative;
  text-align: center;
}

.quote-icon {
  font-size: 4rem;
  color: #ff073a;
  line-height: 1;
  margin-bottom: 20px;
  font-family: serif;
}

.quote-text {
  font-size: 1.4rem;
  font-style: italic;
  color: #ffffff;
  margin-bottom: 20px;
  line-height: 1.6;
}

.quote-author {
  color: #b3b3b3;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

/* Responsive */
@media (max-width: 1024px) {
  .hero-title {
    font-size: 4rem;
  }
  
  .timeline-event:nth-child(odd) .timeline-card,
  .timeline-event:nth-child(even) .timeline-card {
    flex-direction: column;
    text-align: center;
  }
  
  .timeline-header {
    flex-direction: row;
    justify-content: center;
    gap: 30px;
  }
  
  .timeline-connector {
    display: none;
  }
}

@media (max-width: 768px) {
  .history-hero {
    height: 50vh;
    min-height: 400px;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
  }
  
  .stats-container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .stat-card {
    padding: 30px 20px;
  }
  
  .story-title,
  .timeline-title,
  .legacy-title {
    font-size: 2rem;
  }
  
  .timeline-card {
    padding: 30px;
    flex-direction: column !important;
    text-align: center;
  }
  
  .timeline-header {
    flex-direction: row;
    gap: 20px;
  }
  
  .timeline-year {
    font-size: 1.4rem;
    padding: 15px 25px;
    min-width: 120px;
  }
  
  .timeline::before {
    left: 30px;
  }
  
  .timeline-event {
    padding-left: 60px;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .story-text,
  .legacy-text {
    font-size: 1.1rem;
  }
  
  .timeline-card {
    padding: 20px;
  }
  
  .legacy-quote {
    padding: 30px 20px;
  }
  
  .quote-text {
    font-size: 1.2rem;
  }
}