.residents-section {
  padding: 100px 0;
  background: #0a0a12;
  position: relative;
}

.residents-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
}

.section-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 3.5rem;
  font-weight: 800;
  color: #ffffff;
  text-align: center;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.section-subtitle {
  text-align: center;
  color: #b3b3b3;
  font-size: 1.1rem;
  margin-bottom: 80px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 300;
  line-height: 1.6;
}

/* Carrusel de Residents */
.residents-carousel {
  position: relative;
  margin: 0 auto;
  max-width: 1200px;
}

.carousel-track {
  position: relative;
  overflow: hidden;
  margin-bottom: 50px;
}

.carousel-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  position: relative;
  width: 100%;
  align-items: stretch; /* Misma altura para todas las cards */
}

/* Controles del carrusel */
.carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 20;
  transition: all 0.3s ease;
  font-size: 1.2rem;
}

.carousel-control:hover {
  background: rgba(255, 7, 58, 0.3);
  border-color: rgba(255, 7, 58, 0.5);
  transform: translateY(-50%) scale(1.1);
}

.carousel-control.prev {
  left: -80px;
}

.carousel-control.next {
  right: -80px;
}

/* Indicadores */
.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 30px 0;
}

.indicator {
  width: 60px;
  height: 4px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 2px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.indicator.active {
  background: rgba(255, 7, 58, 0.3);
}

.indicator-progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background: #ff073a;
  border-radius: 2px;
  transition: width 0s linear;
}

.indicator.active .indicator-progress {
  width: 100%;
  animation: progress 0s linear;
}

@keyframes progress {
  0% { width: 0%; }
  100% { width: 100%; }
}

/* Contador */
.carousel-counter {
  display: flex;
  align-items: baseline;
  gap: 5px;
  font-weight: 700;
  justify-content: center;
  margin: 10px 0;
}

.current-number {
  font-size: 1.5rem;
  color: #ff073a;
}

.total-number {
  font-size: 1rem;
  color: #b3b3b3;
}

/* Resident Card */
.resident-card {
  background: #1a1a24;
  border: 1px solid #2a2a35;
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  height: 100%; /* Ocupa el 100% de la altura del grid */
  display: flex;
  flex-direction: column;
}

.resident-card:hover {
  transform: translateY(-5px);
  border-color: #ff073a;
  box-shadow: 0 12px 40px rgba(255, 7, 58, 0.15);
}

/* Resident Media Container */
.resident-media {
  position: relative;
  height: 280px;
  background: #2a2a35;
  overflow: hidden;
  border-bottom: 1px solid #2a2a35;
  flex-shrink: 0; /* No se encoge */
}

/* Imagen estática */
.resident-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 0.5s ease;
  position: absolute;
  top: 0;
  left: 0;
}

.resident-photo.hidden {
  opacity: 0;
}

/* Video en hover */
.resident-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.5s ease;
}

/* Video Loading */
.video-loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 15px 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 0.9rem;
  z-index: 10;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid #ff073a;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Mejoras para el overlay existente */
.resident-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgba(10, 10, 18, 0.3) 0%,
    transparent 30%,
    transparent 70%,
    rgba(10, 10, 18, 0.4) 100%
  );
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 25px;
  z-index: 2;
}

.resident-years {
  background: rgba(255, 7, 58, 0.9);
  color: white;
  padding: 8px 12px;
  border-radius: 8px;
  text-align: center;
  font-weight: 700;
  backdrop-filter: blur(10px);
}

.resident-years span {
  display: block;
  font-size: 1.2rem;
  line-height: 1;
}

.resident-years small {
  font-size: 0.7rem;
  opacity: 0.9;
  font-weight: 500;
}

/* Efectos hover mejorados para la card */
.resident-card:hover .resident-media {
  transform: scale(1.02);
  transition: transform 0.3s ease;
}

/* Placeholder mejorado */
.image-placeholder {
  display: none;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2a2a35, #1a1a24);
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}

.image-placeholder i {
  font-size: 3rem;
  color: #ff073a;
  opacity: 0.5;
}

/* Resident Content - MEJORADO PARA ALTURAS SINCRONIZADAS */
.resident-content {
  padding: 30px;
  display: flex;
  flex-direction: column;
  flex: 1; /* Ocupa todo el espacio disponible */
}

.resident-header {
  margin-bottom: 20px;
  text-align: center;
  flex-shrink: 0;
}

.resident-name {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 8px;
  letter-spacing: 0.5px;
}

.resident-genre {
  color: #ff073a;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
}

.resident-stats {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 25px;
  padding-bottom: 20px;
  border-bottom: 1px solid #2a2a35;
  flex-shrink: 0;
}

.stat {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #b3b3b3;
  font-size: 0.9rem;
}

.stat i {
  color: #ff073a;
  font-size: 0.8rem;
}

.resident-bio {
  color: #b3b3b3;
  line-height: 1.7;
  margin-bottom: 25px;
  text-align: center;
  font-size: 0.95rem;
  flex-shrink: 0;
}

.resident-specialties {
  margin-bottom: 30px;
  flex-shrink: 0;
}

.resident-specialties h4 {
  color: #ffffff;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 600;
}

.specialties-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
}

.specialty {
  background: rgba(255, 7, 58, 0.1);
  color: #ff073a;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid rgba(255, 7, 58, 0.2);
  transition: all 0.3s ease;
}

.specialty:hover {
  background: rgba(255, 7, 58, 0.2);
  transform: translateY(-1px);
}

.soundcloud-section {
  margin-bottom: 25px;
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.soundcloud-section h4 {
  color: #ffffff;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 600;
  flex-shrink: 0;
}

.soundcloud-player {
  background: #0a0a12;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #2a2a35;
  transition: all 0.3s ease;
  flex: 1;
  min-height: 0;
}

.soundcloud-player:hover {
  border-color: #ff073a;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.soundcloud-player iframe {
  width: 100%;
  height: 100%;
  min-height: 300px;
}

.resident-links {
  display: flex;
  justify-content: center;
  gap: 15px;
  padding-top: 20px;
  border-top: 1px solid #2a2a35;
  flex-shrink: 0;
}

.resident-links .social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  background: #2a2a35;
  border: 1px solid #3a3a45;
  border-radius: 8px;
  color: #b3b3b3;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  text-decoration: none;
}

.resident-links .social-link:hover {
  background: #ff073a;
  border-color: #ff073a;
  color: white;
  transform: translateY(-2px);
}

/* Responsive */
@media (max-width: 1200px) {
  .carousel-control.prev {
    left: -40px;
  }
  
  .carousel-control.next {
    right: -40px;
  }
}

@media (max-width: 1024px) {
  .carousel-grid {
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .residents-section {
    padding: 80px 0;
  }
  
  .section-title {
    font-size: 2.5rem;
  }
  
  /* En móvil: 1 resident por slide */
  .carousel-grid {
    grid-template-columns: 1fr;
    gap: 30px;
    align-items: start; /* En móvil no necesitan misma altura */
  }
  
  .carousel-track {
    margin-bottom: 40px;
  }
  
  .carousel-control {
    width: 50px;
    height: 50px;
    font-size: 1rem;
  }
  
  .carousel-control.prev {
    left: -20px;
  }
  
  .carousel-control.next {
    right: -20px;
  }
  
  .resident-content {
    padding: 25px;
  }
  
  .resident-media {
    height: 240px;
  }
  
  .resident-stats {
    gap: 20px;
  }
  
  .resident-name {
    font-size: 1.6rem;
  }
  
  .soundcloud-player iframe {
    min-height: 250px;
  }
  
  .carousel-indicators {
    margin: 25px 0;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 2rem;
  }
  
  .resident-media {
    height: 200px;
  }
  
  .resident-overlay {
    padding: 15px;
  }
  
  .resident-years {
    padding: 6px 10px;
  }
  
  .resident-years span {
    font-size: 1rem;
  }
  
  .carousel-control {
    width: 40px;
    height: 40px;
  }
  
  .carousel-control.prev {
    left: -10px;
  }
  
  .carousel-control.next {
    right: -10px;
  }
  
  .indicator {
    width: 40px;
  }
  
  .current-number {
    font-size: 1.3rem;
  }
  
  .resident-content {
    padding: 20px;
  }
  
  .resident-stats {
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }
  
  .soundcloud-player iframe {
    min-height: 200px;
  }
  
  .carousel-indicators {
    margin: 20px 0;
  }
}