.adminLoadingOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 10, 18, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  backdrop-filter: blur(10px);
}

.adminLoadingContent {
  text-align: center;
  position: relative;
}

.adminLogoContainer {
  width: 120px;
  height: 120px;
  margin: 0 auto 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.adminLoadingLogo {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: drop-shadow(0 0 20px rgba(255, 7, 58, 0.3));
}

.adminLogoFallback {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #ff073a, #8a2be2);
  border-radius: 50%;
  color: white;
  font-family: 'Exo 2', sans-serif;
  font-size: 1.5rem;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.adminLoadingRing1,
.adminLoadingRing2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid rgba(255, 7, 58, 0.2);
  border-radius: 50%;
  pointer-events: none;
}

.adminLoadingRing1 {
  width: 140px;
  height: 140px;
}

.adminLoadingRing2 {
  width: 160px;
  height: 160px;
  border-color: rgba(138, 43, 226, 0.2);
}

/* Efecto de pulso sutil */
.adminPulseEffect {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 180px;
  height: 180px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.1) 0%, transparent 70%);
  border-radius: 50%;
  pointer-events: none;
}

.adminLoadingMessage {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  font-weight: 500;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.adminLoadingDots {
  color: #ff073a;
  font-size: 1.5rem;
  font-weight: 800;
  letter-spacing: 3px;
}

/* Loading overlay para contenido específico */
.adminContentLoading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 10, 18, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  border-radius: 25px;
  backdrop-filter: blur(5px);
}